.TodoSearch {
    background: #f9fbfc;
    border-radius: 50px;
    border: 2px solid #202339;
    margin: 12px 36px;
    height: 64px;
    width: calc(100% - 20%);
    font-size: 24px;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 400;
    color: #1e1e1f;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::placeholder {
    color: #A5A5A5;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 400;
}

.TodoSearch:focus {
    outline-color: #61DAFA;
}